export async function getRecommendations(
    courseName: string,
    teeTime: string,
    type: 'PRE_ROUND' | 'POST_ROUND'
): Promise<Restaurant[]> {
    // Simulate delay
    await new Promise(resolve => setTimeout(resolve, 600));

    // Logic: 
    // PRE_ROUND: Must open 1h before teeTime. Menu: Soup/Quick.
    // POST_ROUND: Relaxed. Exclude cooking > 30m? (Simulated by excluding certain types or just mocked list)

    // In a real app, we would query Naver/Kakao Maps API with "Restaurant near {courseName}"
    // and filter by open hours and keywords.

    return MOCK_RESTAURANTS.filter(r => r.category === type)
        .sort((a, b) => b.rating - a.rating)
        .slice(0, 3);
}
